//функция создания случайных челых чисел из интервала [a;b]
function randint(min, max) { return Math.floor(min + Math.random() * (max + 1 - min)) }
//функция получения случайных элементов массива
Array.prototype.random = function(n=1) {if(n==1)return this[randint(0,this.length-1)];
else return this.sort(()=>0.5-Math.random()).slice(0,n)}
//функция создания функции из текста
function getFunction(txt) {return new Function('{return function(x){'+txt+'}}')()}
